.input:focus {
    outline: none;
}

.input_wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
}

.input_wrapper img {
    background-color: #1A1A1A;
    padding: 0 2vw 0 2.5vw;
    border-radius: 10px 0 0 10px;
}

.input {
    border: none;
    font-size: larger;
    color: white;
    background-color: #1A1A1A;
    width: 90vw;
    height: 7vh;
    border-radius: 0 10px 10px 0;
}